import {React, useEffect, useState, useRef} from 'react';
import Table from 'react-bootstrap/Table';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import DatePicker from "react-datepicker"
import "react-datepicker/dist/react-datepicker.css"
import { ja } from 'date-fns/locale';
import { format } from 'date-fns';



const Todo = ({visibleTodo, toggleTodo, show, onHide, handleOnSave}) => {
  // const handleTodoClick = () => {
  //   toggleTodo(todo.id);
  // };
  console.log('visibleToso',visibleTodo);
  //
  //const initialDate = new Date()
  const [startDate, setStartDate] = useState(null);
console.log('set',startDate);
  useEffect(() => {
    //
    if (visibleTodo === null) {
      //
      return;
    } else {
      console.log('',new Date(visibleTodo.created_at));
      //DatePickerselecteddate
      setStartDate(visibleTodo.created_at ? new Date(visibleTodo.created_at) : visibleTodo.created_at);
    }
  },[visibleTodo]);
 

  const handleDateChange = (date) => {
    //
    setStartDate(date)
  
  }

  //
  const amountRef = useRef();
  const memoRef = useRef();

  console.log('visibleTodo', visibleTodo);

  const handleSave = () => {
    //
    const amount = amountRef.current.value;
    console.log('amount',amount);
    visibleTodo.amount = amount;
    //null
    visibleTodo.created_at = startDate !== null ?? format(startDate, 'yyyy-MM-dd', { locale: ja });
    visibleTodo.memo = memoRef.current.value;

    handleOnSave(visibleTodo);
    amountRef.current.value = null;
    memoRef.current.value = null;
    onHide(); // 
  };

  return (
    <>
      <div>
        {/* <label>
          <input type="checkbox" checked={todo.completed} onChange={handleTodoClick}/>
          <button></button>
        </label>
        {todo.name} */}
      </div>
       <Modal show={show} onHide={onHide} centered size="lg">
      <Modal.Header closeButton>
        <Modal.Title> / </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {/* name */}
          <div className="mb-1 py-1 bg-sky-200 font-bold text-xl text-center shadow-lg rounded-md">{visibleTodo?.name}</div>
            <div className="flex-1 bg-gray-100 p-6">
            <div className="crayon-orange-g-narrow p-2 w-fit"></div>
            <div className="flex">
              <input
                defaultValue={visibleTodo?.amount}
                type="number"
                ref={amountRef}
                className="form-control my-2"
                style={{ width: "8rem" }}
              />
              <div className="pt-3 px-2"></div>
            </div>
            <div className="crayon-orange-g-narrow p-2 w-fit"></div>
            <DatePicker
              locale={ja}
              selected={startDate}
              onChange={handleDateChange}
              dateFormat="yyyy-MM-dd"
              className="form-control my-2"
            />
            <div className="crayon-orange-g-narrow p-2 w-fit"></div>
            <input
              defaultValue={visibleTodo?.memo}
              type="text"
              ref={memoRef}
              className="form-control my-2"
            />
         </div> 
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={onHide}></Button>
        <Button variant="primary" onClick={handleSave}></Button>
      </Modal.Footer>
    </Modal>
    </>
  )
}

export default Todo